import { Component, OnInit } from '@angular/core';
import { SupportService } from '../../../../../auth/_services/support.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DataService } from '../../../../../_services/data.service';

import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-support-details',
    templateUrl: "support-details.component.html",
    styles: []
})
export class SupportDetailsComponent implements OnInit {
    private formSubmitAttempt: boolean;
    private supportDetails: any = {};
    private isReadOnly: any;
    constructor(private toastr: ToastrService, private router: Router, private _dataService: DataService, private route: ActivatedRoute, private _supportService: SupportService) { }

    ngOnInit() {


        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.support_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/


        this.isReadOnly = 'readonly';
        this._dataService.setloaderState(false);
        this._dataService.setactiveMenu('support');
        this.route.params.subscribe(
            params => {
                localStorage.setItem('section', 'support');
                this._supportService.getById(params.id).subscribe(
                    data => {

                        this.supportDetails = data.success;
                    }
                );
            }
        );
    }
}